import React from "react";
import Chess from "./Chess";

function Sample() {
  const sampleCode = `componentDidMount() {
    fetch("https://qunliw.github.io/chess.json")
      .then((resp) => resp.json())
      .then((chessResp) => {
        this.setState({ chessData: chessResp });
      });
  }`;
  return (
    <div>
      <Chess />
      <div className="container-1">
        <div className="box-1">
          <h3>Sample JS code</h3>
          <p>{sampleCode}</p>
        </div>
        <div className="box-2">
          <h3>Why Single Page Applications (SPAs)?</h3>
          <p>
            A much-improved user experience due to dynamically rewriting the
            current web page with new data without reloading full page
          </p>
        </div>
      </div>
    </div>
  );
}

export default Sample;
