import React from "react";
import contactMe from "./contactMe.png";

function Contact() {
  return (
    <section id="showcase">
      <div class="contact">
        <img src={contactMe} align="center" alt="Smiley face" height="100" />
        <h2>A friendly hello or kind suggestions</h2>
        <form className="contact-form">
          <input
            name="name"
            type="text"
            className="form-control"
            placeholder="Your Name"
            required
          />
          <br />
          <input
            name="email"
            type="email"
            className="form-control"
            placeholder="Your Email"
            required
          />
          <br />
          <textarea
            name="message"
            className="form-control"
            placeholder="Message"
            row="8"
          ></textarea>
          <br />
          <input
            type="submit"
            className="form-control submit"
            value="SEND MESSAGE"
          />
        </form>
      </div>
    </section>
  );
}

export default Contact;
